import React from 'react';
import { createRoot } from 'react-dom/client';
import DateIcon from './DateIcon';

// Wrap the component in an error boundary to prevent crashing
const ErrorBoundary = ({ children }) => {
  try {
    return children;
  } catch (error) {
    console.error("React error caught:", error);
    return <div>Error displaying date</div>;
  }
};

//* Find all date containers on the page
document.addEventListener('DOMContentLoaded', () => {
  console.log('DateApp starting - looking for containers');
  const dateContainers = document.querySelectorAll('[data-react-date]');
  console.log(`Found ${dateContainers.length} date containers`);
  
  dateContainers.forEach((container, index) => {
    try {
      console.log(`Processing date container ${index}`);
      const date = container.getAttribute('data-date');
      const className = container.getAttribute('data-class') || '';
      
      console.log(`Container ${index} date:`, date);
      console.log(`Container ${index} className:`, className);
      
      //* Create a root using the React 18 API
      const root = createRoot(container);
      root.render(
        <ErrorBoundary>
          <DateIcon 
            date={date}
            className={className}
          />
        </ErrorBoundary>
      );
    } catch (error) {
      console.error(`Error processing container ${index}:`, error);
      container.innerHTML = '<span>ðŸ“…</span> <span>Error</span>';
    }
  });
}); 